<template>
  <div id="app">
    <div class="align-center">
      <h2>Preencha os campos e copie o link</h2>
    <div class="input-wrapper">
      <span class="label">Guest Name</span>
      <span class="label-subtitle">Optional</span>
      <input v-model="name" />
    </div>
    <div class="input-wrapper">
      <span class="label">Booking ID</span>
      <span class="label-subtitle">Required</span>
      <input
        v-model="bookid"
        placeholder="00000000"
        type="number"
        minlength="8"
        maxlength="8"
        required
      />
    </div>
    <div class="input-wrapper">
      <span class="label">Rate</span>
      <span class="label-subtitle">Optional</span>
      <input v-model="rate" placeholder="00.00" type="number" step=".01" />
    </div>
    <div class="input-wrapper">
      <span class="label">Guests</span>
      <span class="label-subtitle">Required</span>
      <input v-model="guests" placeholder="1" type="number" min="1" max="4" />
    </div>
    <div class="input-wrapper">
      <span class="label">Nights</span>
      <span class="label-subtitle">Required</span>
      <input v-model="nights" placeholder="1" type="number" min="1" />
    </div>
    <div class="total-wrapper">
      <span class="total">Total: {{ total }}€</span>
    </div>
    <div>
      <input
        v-on:focus="$event.target.select()"
        ref="clone"
        readonly
        :value="text"
      />
      <button :disabled="disabled" @click="copy">Copy</button>
      <button @click="reset">Reset</button>
    </div>
    <div v-if="!disabled">
      <h4>Link examples:</h4>
      <a :href="text"
        >Pagar {{ rate ? rate + "€ + " : "" }} {{ guests * nights * 2 }}€ Taxa
        Turística Municipal</a
      ><br />
      <a :href="text"
        >Pay {{ rate ? rate + "€ + " : "" }} {{ guests * nights * 2 }}€ Local
        Turistic Tax</a
      >
    </div>

    <div v-if="!disabled">
      <h4>Message examples:</h4>
      <div class="message-examples">
        <p>
          Olá {{ name || "NAME" }},<br />
          Segue o link para pagamento:
        </p>
        <a :href="text"
          >Pagar {{ rate ? rate + "€ + " : "" }} {{ guests * nights * 2 }}€ Taxa
          Turística Municipal</a
        >
      </div>
      <div class="message-examples">
        <p>
          Hi {{ name || "NAME" }},<br />
          The payment link is down below:
        </p>
        <a :href="text"
          >Pay {{ rate ? rate + "€ + " : "" }} {{ guests * nights * 2 }}€ Local
          Turistic Tax</a
        >
      </div>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data: function () {
    return {
      name: "",
      bookid: null,
      rate: null,
      guests: 1,
      nights: 1,
    };
  },
  computed: {
    total: function () {
      return +this.rate + +this.guests * +this.nights * 2;
    },
    text: function () {
      return `https://beds24.com/bookpay.php?bookid=${this.bookid}&pay=${this.total}`;
    },
    disabled: function () {
      return !this.bookid || !this.total;
    },
  },
  methods: {
    copy() {
      this.$refs.clone.focus();
      document.execCommand("copy");
    },
    reset() {
      this.name = "";
      this.bookid = null;
      this.rate = null;
      this.guests = 1;
      this.nights = 1;
    },
  },
};
</script>

